.App {
  text-align: center;
  background-color: #ffe4d6;
}

/*header design*/
.header {
  background-color: #da6c70;
  height: 4rem;
  display: flex;
  flex-direction: row;
}

/*logo design*/
.logoContainer {
  display: flex;
  width: 50%;
}

.logo {
  width: 10rem;
  height: 100%;
  margin-left: 20%;
}

/*header menu*/
.headerItems {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

/*nav bar items design*/
.recipeMenu,
.mealPlanMenu {
  font-family: 'Handlee', cursive;
  font-size: 1.3rem;
  color: #4b4850;
}

.headerItems h3:hover {
  cursor: pointer;
  font-size: 1.8rem;
  color: black;
}

/*nav bar items when clicked*/
.click {
  font-family: 'Handlee', cursive;
  font-size: 1.8rem;
  color: black;
}