/*buttons cntainer*/
.buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

/*buttons design*/
.buttons button {
    margin: 2rem;
    width: 15rem;
    border-radius: 1rem;
    font-family: 'Pangolin', cursive;
    font-size: 1.3rem;
}

.buttons button:hover {
    cursor: pointer;
    color: #4b4850;
    background-color: #d7c1c6;
}

/*buttons when not clicked*/
.morningMenu,
.lunchMenu,
.afternoonMenu {
    color: #4b4850;
    background-color: #ffb4a2;
    text-shadow: 2px 2px 3px #eff7f6;
}

/*buttons when clicked*/
.clicked {
    color: #eff7f6;
    background-color: #4b4850;
}