/*recipe item component design*/
.recipeItem {
    width: 20rem;
    height: 20rem;
    margin: 2rem;
    border: 2px solid #4b4850;
    border-radius: 2rem;
    background-color: #eff7f6;
}

.recipeItem:hover {
    cursor: pointer;
    background-color: #d7c1c6;
}

/*recipe card component design*/
.recipeCard {
    width: 80rem;
    height: 39rem;
    margin-top: 3rem;
    background-color: #eff7f6;
    border: 2px solid #4b4850;
    display: flex;
    flex-direction: column;
    font-family: 'Pangolin', cursive;
}

.backButton {
    width: 5rem;
    background-color: #ffb4a2;
    margin: 1rem;
    border-radius: 1rem;
}

.title {
    font-size: 3rem;
    text-align: center;
    margin: 0;
}

.recipeCardBody {
    display: flex;
    flex-direction: row;
}

.picture {
    width: 50%;
}

.description {
    width: 50%;
    margin: 2rem 0 4rem 0;
}


.links button {
    width: 45.25%;
    font-family: 'Pangolin', cursive;
    font-size: 1.5rem;
}

.ingredientsMenu,
.procedureMenu {
    color: #4b4850;
    background-color: #ffb4a2;
    text-shadow: 2px 2px 3px #eff7f6;
}

.recipeBody {
    width: 85%;
    height: 22rem;
    border: 2px solid #4b4850;
    background-color: #ffe4d6;
    padding: 1rem;
    font-size: 1.2rem;
}